<template>
  <div class="container with-nav">
    <app-alert 
    v-if="alert" 
    :title="'Работаем с Composition Api'"
    @close="closeAlert" 
    :type="'primary'"></app-alert>
    <div class="card">
      <h1>Переиспользование</h1>

      <button class="btn primary" @click="toggleAlert">{{alert ? 'Скрыть сообщение' : 'Показать сообщение'}}</button>
      <button class="btn" @click="navigate">Перейти на главную</button>
    </div>
  </div>
</template>

<script>
import {ref} from 'vue'
import {useRouter, useRoute} from 'vue-router';
import AppAlert from '../AppAlert.vue';
export default {
  setup() {

    const title = ref('Работаем с Composition Api');
    const alert = ref(false);


    const router = useRouter();
    const route = useRoute();
    

    const navigate = () => router.push('/');

    const closeAlert = () => alert.value = false;
    const toggleAlert = () => alert.value = !alert.value;
    

    return {
      navigate,
      title,
      alert,
      toggleAlert,
      closeAlert,
    }
  },
  components: {
    AppAlert,
  }
}
</script>